<template>
    <div>
        <div>
            <div class="seven ui buttons">
                <button class="ui button" v-on:click="toggleTabs(monday)" >Monday</button>
                <button class="ui button" v-on:click="toggleTabs(tuesday)">Tuesday</button>
                <button class="ui button" v-on:click="toggleTabs(wednesday)">Wednesday</button>
                <button class="ui button " v-on:click="toggleTabs(thursday)">Thursday</button>
                <button class="ui button" v-on:click="toggleTabs(friday)">Friday</button>
                <button class="ui button" v-on:click="toggleTabs(saturday)">Saturday</button>
                <button class="ui button" v-on:click="toggleTabs(sunday)">Sunday</button>
            </div>
        </div>

        <div v-for="info in fetchedData" :key="info.id"  >
            <div> 
                <div class="position_class" v-if="getWeekDay(info.dt)=='Monday'" v-bind:class="monday" >
                    <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
                </div> 
            </div>
            <div  class="position_class" v-if="getWeekDay(info.dt)=='Tuesday'" v-bind:class="tuesday">
                     <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div>
            <div class="position_class" v-if="getWeekDay(info.dt)=='Wednesday'" v-bind:class="wednesday">
                <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div> 
            <div class="position_class" v-if="getWeekDay(info.dt)=='Thursday'" v-bind:class="thursday">
                <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div> 
            <div class="position_class" v-if="getWeekDay(info.dt)=='Friday'" v-bind:class="friday">
                <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div> 
            <div class="position_class" v-if="getWeekDay(info.dt)=='Saturday'" v-bind:class="saturday">
                <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div>  
            <div class="position_class" v-if="getWeekDay(info.dt)=='Sunday'" v-bind:class="sunday">
               <div class="inner_component">
                        <h3 >{{getWeekDay(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >{{getHMS(info.dt)}}</h3>
                    </div>
                    <div class="inner_component">
                        <h3 >max: {{max_temp}} &#8451; min: {{min_temp}} &#8451;</h3>
                    </div>
                    <div class="inner_component">
                        <img v-bind:src="icon" alt="weather info" >
                    </div> 
            </div>         
        </div>
    </div>
</template>

<script>


export default {
    props:['fetchedData','weekday','hourMin','max_temp','min_temp','icon'],
    name: "indigo-tabs",
    data(){
        return{
          monday:'hide_content 1',
          tuesday:'hide_content 2',
          wednesday:'hide_content 3',
          thursday:'hide_content 4',
          friday:'hide_content 5',
          saturday:'hide_content 6',
          sunday:'hide_content 7'
         
        }
    },
    methods:{
       getWeekDay(unixTime){
        const date = new Date(unixTime*1000);
        const resultData=date.toLocaleDateString('en-US',{weekday: "long"});
        return resultData;
    },
     getHMS(unixTime){
        const date = new Date(unixTime*1000);
        var hour = date.getHours();
        var minutes = date.getMinutes();
        return hour+":"+minutes;
    },
    toggleTabs: function(toggleClass){
      if(this.monday===toggleClass){
          this.monday="display";
          this.tuesday='hide_content 2',
          this.wednesday='hide_content 3',
          this.thursday='hide_content 4',
          this.friday='hide_content 5',
          this.saturday='hide_content 6',
          this.sunday='hide_content 7'
      }
      if(this.tuesday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='display',
          this.wednesday='hide_content 3',
          this.thursday='hide_content 4',
          this.friday='hide_content 5',
          this.saturday='hide_content 6',
          this.sunday='hide_content 7'
      }
      if(this.wednesday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='hide_content 2',
          this.wednesday='display',
          this.thursday='hide_content 4',
          this.friday='hide_content 5',
          this.saturday='hide_content 6',
          this.sunday='hide_content 7'
      }
      if(this.thursday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='hide_content 2',
          this.wednesday='hide_content 3',
          this.thursday='display',
          this.friday='hide_content 5',
          this.saturday='hide_content 6',
          this.sunday='hide_content 7'
      }
       if(this.friday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='hide_content 2',
          this.wednesday='hide_content 3',
          this.thursday='hide_content 4',
          this.friday='display',
          this.saturday='hide_content 6',
          this.sunday='hide_content 7'
      }
      if(this.saturday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='hide_content 2',
          this.wednesday='hide_content 3',
          this.thursday='hide_content 4',
          this.friday='hide_content 5',
          this.saturday='display',
          this.sunday='hide_content 7'
      }
      if(this.sunday===toggleClass){
          this.monday="hide_content 1";
          this.tuesday='hide_content 2',
          this.wednesday='hide_content 3',
          this.thursday='hide_content 4',
          this.friday='hide_content 5',
          this.saturday='hide_content 6',
          this.sunday='display'
      }
    }
    }
    
}
</script>

<style scoped>
.hide_content{
    display: none;
}
.inner_component{
  display: inline-block;
  width:25%;
  text-align: center;
  font-size: 30px;
  color:white;
  height: 50px;
}
.inner_component img{
    margin-bottom: -19.2px;
}
.position_class{
  position: relative;
  background-color:#ccdfb673 ;
  height: 50px;
  margin:5px;
  margin-bottom: 5px;
}


</style>