<template>
<div class="ui container custom_container">
  <h1 style="color:white">Use this app, and get 5 day forecast for every 3 hours!!!</h1>
  <div class="ui segment ">
    <div class="ui form ">
    <input type="text" v-model="searchResult"   @keypress="fetchWeather" placeholder="Enter city name"/>  
  </div>
</div>
  <WeatherDetail 
  v-bind:fetchedData='fetchedData'
  v-bind:weekDay='weekday'
  v-bind:hourMin='hourMin'
  v-bind:max_temp='max_temp'
  v-bind:min_temp='min_temp'
  v-bind:icon='icon'
  />

  
</div>
</template>

<script>
import WeatherDetail from './components/WeatherDetail.vue';
export default {
  name: 'App',
  components: {
    WeatherDetail,
  },
  data(){
    return{
      searchResult:'',
      api:'209971d14b160f37398715e6bfc1d2b5',
      fetchedData:[],
      weekday:'',
      hourMin:'',
      max_temp:'',
      min_temp:'',
      icon:''
    }
  },
  methods:{
    fetchWeather(e){
    // Simple GET request using fetch
    if(e.key == "Enter"){   
    const apiUrl = `https://api.openweathermap.org/data/2.5/forecast?q=${this.searchResult}&appid=${this.api}`;
    fetch(apiUrl)
      .then(response => response.json())
      .then(data => {
        if(data.cod==="200"){
          this.fetchedData=data.list;
          data.list.map(info=>{
            this.weekday=this.getWeekDay(info.dt);
            this.hourMin=this.getHMS(info.dt);
            this.max_temp=Math.round(info.main.temp_max-273.15);
            this.min_temp=Math.round(info.main.temp_min-273.15);
            this.icon="http://openweathermap.org/img/w/"+info.weather[0].icon+".png";
          })

        }
        else if(data.cod=="404"){
          alert("Enter location correctly")
        }  
    })
    .catch(eror=>{console.log(eror)});
      }
    },
    getWeekDay(unixTime){
        const date = new Date(unixTime*1000);
        const resultData=date.toLocaleDateString('en-US',{weekday: "long"});
        console.log(resultData);
        return resultData;
    },
     getHMS(unixTime){
        const date = new Date(unixTime*1000);
        var hour = date.getHours();
        var minutes = date.getMinutes();
        return hour+":"+minutes;
    }
  },
}
</script>

